import React, { useState } from "react";
import { HiMenuAlt4 } from "react-icons/hi";
import { AiOutlineClose } from "react-icons/ai";
import "../style/navbar.css";

const NavBarItem = ({ title, classprops = "", onClick }) => (
  <li
    className={`mx-4 cursor-pointer text-lg text-white transition-all duration-300 hover:text-[#ffcc00] ${classprops}`}
    onClick={onClick}
  >
    {title}
  </li>
);

const Navbar = () => {
  const [toggleMenu, setToggleMenu] = useState(false);

  return (
    <nav className="w-full flex justify-between items-center p-4 bg-black shadow-lg relative z-[10001] navbar">
      <div className="flex-initial justify-center items-center text-2xl font-bold text-white hover:text-[#ffcc00] transition-all cursor-pointer">
        HealthAI 🔬
      </div>

      <ul className="text-white md:flex hidden list-none flex-row justify-between items-center flex-initial">
        {["关于我们", "博客", "健康评分", "症状输入", "数据上传"].map((item, index) => (
          <NavBarItem key={item + index} title={item} />
        ))}

        <li className="relative group cursor-pointer mx-4 text-lg text-white transition-all duration-300 hover:text-[#ffcc00]">
          测试工具
          <ul className="absolute left-0 mt-2 w-36 bg-black shadow-lg opacity-0 invisible group-hover:opacity-100 group-hover:visible transition-opacity duration-300">
            <li className="px-4 py-2 hover:bg-gray-700 cursor-pointer">评分测试</li>
            <li className="px-4 py-2 hover:bg-gray-700 cursor-pointer">界面测试</li>
            <li className="px-4 py-2 hover:bg-gray-700 cursor-pointer">模型测试</li>
          </ul>
        </li>

        <li className="bg-gradient-to-r from-pink-500 to-violet-600 py-2 px-6 mx-4 rounded-full cursor-pointer hover:scale-105 transition-transform text-white font-semibold shadow-lg">
          登录
        </li>
      </ul>

      <div className="flex md:hidden">
        {!toggleMenu ? (
          <HiMenuAlt4
            fontSize={28}
            className="text-white cursor-pointer hover:text-[#ffcc00]"
            onClick={() => setToggleMenu(true)}
          />
        ) : (
          <>
            <AiOutlineClose
              fontSize={28}
              className="text-white cursor-pointer hover:text-[#ffcc00]"
              onClick={() => setToggleMenu(false)}
            />
            <ul className="fixed top-0 right-0 p-4 w-[70vw] h-screen bg-black shadow-2xl flex flex-col justify-start items-end list-none z-10 animate-slide-in">
              <li className="text-xl w-full mb-4">
                <AiOutlineClose onClick={() => setToggleMenu(false)} />
              </li>
              {["关于我们", "博客", "健康评分", "症状输入", "数据上传", "登录"].map((item, index) => (
                <NavBarItem key={item + index} title={item} classprops="my-2 text-lg" />
              ))}
            </ul>
          </>
        )}
      </div>
    </nav>
  );
};

export default Navbar;
