import React from "react";
import "../style/footer.css";
import qrCode from "../assets/qrcode.png"; // 你需要在 assets 文件夹中放一张二维码图片

export default function Footer() {
  return (
    <footer className="footer">
      <div className="footer-grid">
        <div>
          <h2>联系方式</h2>
          <p>Email: <a href="mailto:healthai@support.com">healthai@support.com</a></p>
          <p>微信客服 / 微信公众号</p>
          <img src={qrCode} alt="扫码关注" className="qr-code" />
        </div>

        <div>
          <h2>功能导航</h2>
          <ul>
            <li><a href="#">健康评分</a></li>
            <li><a href="#">症状输入</a></li>
            <li><a href="#">数据上传</a></li>
            <li><a href="#">模型解释</a></li>
          </ul>
        </div>

        <div>
          <h2>用户支持</h2>
          <ul>
            <li><a href="#">常见问题</a></li>
            <li><a href="#">反馈建议</a></li>
            <li><a href="#">用户协议</a></li>
            <li><a href="#">隐私政策</a></li>
          </ul>
        </div>

        <div>
          <h2>社区与合作</h2>
          <ul>
            <li><a href="#">GitHub 项目</a></li>
            <li><a href="#">API 文档</a></li>
            <li><a href="#">成为合作伙伴</a></li>
          </ul>
        </div>
      </div>

      <div className="footer-bottom">
        © {new Date().getFullYear()} HealthAI | 赋能每一个人的健康未来
      </div>
    </footer>
  );
}
